.home {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}
.home-image {
    width: 100%;
    height: 600px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0,0));
    z-index: -1;
    margin-bottom: -350px;
}
.display-grid {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 10px;
    margin-top: 150px;
}
