.products {
    display: flex;
    flex-direction: column;
    /* max-width: 532px; */
    justify-content: flex-end;
    align-items: center;
    max-height: 400px;
    background-color: white;
    margin: 10px;
    padding: 20px;
    width: 100%;
    z-index: 1;
    /* box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px; */
}
.products > img {
    max-height: 200px;
    width: 100%;   
    object-fit: contain;
    margin-bottom: 15px;
}
.products-rating {
    display: flex;
}
.produst-info {
height: 100px;
margin-bottom: 15px;
}
.products-proce {
    margin-top: 5px;
}
.products > button {
    border: thin;
    background-color: orange;
}
.home-product {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
.products-rating > p {
    color: #d6b022;
}