* {
    text-decoration: none;
}
.header {
    background: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header-logo {
    /* width: 100px; */
    object-fit: contain;
    margin-top: 18px;
    margin-left: 20px;
    margin-right: 20px;
}
.header-search-icon {
    background-color: #cd9042;
    height: 22px !important;
    padding: 5px;
}
.header-search {
    display: flex;
    flex: 1;
    /* margin-left: 80px; */
}
.header-search-box {
    height: 12px;
    border: none;
    padding: 10px;
    width: 100%;
    height: 30px;
}
.header-nav {
    display: flex;
    justify-content: space-evenly;
    margin-left: 25px;
    margin-right: 20px;
    position: static;
}
.header-link {
    text-decoration: none;
    color: white;
}
.header-nav-option {
display: flex;
flex-direction: column;
margin-left: 10px;
margin-right: 10px;
}
.header-nav-option-one {
    font-size: 13px;
    font-weight: 800;
}
.header-nav-option-basket {
    display: flex;
}
.header-basket-counts {
    margin-left: 10px;
    margin-right: 10px;
}
p.add-jus {
    font-size: 30px;
    color: #ffffff;
    text-decoration: none;
}
h5.add-left {
    font-size: 20px; 
    text-align: center;
    font-style: italic;
    margin-bottom: 5px;
    color: #ffffff;
}